// Import utilities
import { PUBLIC_ROUTES, PRIVATE_ROUTES } from 'constants/routes';
import IdGenerationProvider from 'Context/IdGeneration';
import { LangContext } from 'Context/Language';
import PrivateRoute from 'Context/PrivateRoute';
import UserContextProvider from 'Context/UserContext';
import Navigation from 'Layout/NavigationBar/Navigation';
import React, { useContext } from 'react';
import { HashRouter, Route, Switch } from 'react-router-dom';
import './utils/i18n';

function App() {
  const language = useContext(LangContext);

  const changeLang = (lang: 'en' | 'es') => {
    language.setLanguage(lang);
  };

  return (
    <>
      <UserContextProvider>
        <IdGenerationProvider>
          <HashRouter basename={process.env.PUBLIC_URL}>
            <Navigation changeLang={(lang) => changeLang(lang)} />
            <Switch>
              {PUBLIC_ROUTES.map((route) => (
                <Route
                  exact
                  key={route.id}
                  path={route.path}
                  component={route.component}
                />
              ))}
              {PRIVATE_ROUTES.map((route) => (
                <PrivateRoute
                  exact
                  key={route.id}
                  path={route.path}
                  render={route.component}
                />
              ))}

              <Route render={() => <div>Not found</div>} />
            </Switch>
          </HashRouter>
        </IdGenerationProvider>
      </UserContextProvider>
    </>
  );
}

export default App;
